import React from 'react';
import {
  StyleSheet,
  View,
  Text,
  TextInput,
  Button,
  Image,
  KeyboardAvoidingView,
} from 'react-native';
import CONFIG from '../config';

const ERROR = {
  PARSE_FAIL: {code: 10001, msg: 'Param parse fail'},
  MYSQL_FAIL: {code: 20001, msg: 'Connect SQL fail'},
  ACCOUNT_EXIST: {code: 30001, msg: 'Account exist'},
  ACCOUNT_NOT_EXIST: {code: 30002, msg: 'Account not exist'},
  WRONG_PASSWORD: {code: 30003, msg: 'Password is wrong'},
};

export default class Register extends React.Component {
  static navigationOptions = {
    title: '登录/注册页面',
    headerStyle: {
      backgroundColor: '#66ccff',
    },
  };
  //==========
  //life cycle
  //==========
  constructor(props) {
    super(props);
    this.message = [];
    this.state = {
      password: '',
      account: '',
      userId: '',
      tips: '只支持大小写英文和数字',
      isFetching: false,
    };

    this.onTypeAccount = this.onTypeAccount.bind(this);
    this.onTypePassword = this.onTypePassword.bind(this);
    this.onRegister = this.onRegister.bind(this);
    this.onLogin = this.onLogin.bind(this);
    this.registerAccount = this.registerAccount.bind(this);
    this.login = this.login.bind(this);
  }

  componentDidMount() {}

  componentWillUnmount() {}
  //==========
  // event
  //==========
  onRegister() {
    if (!this.state.account && !this.state.password) {
      return;
    }
    this.registerAccount();
  }
  onLogin() {
    this.login();
  }
  onTypeAccount(text) {
    text = text.replace(/[^a-zA-Z0-9]/g, '');
    this.setState({account: text});
  }
  onTypePassword(text) {
    text = text.replace(/[^a-zA-Z0-9]/g, '');
    this.setState({password: text});
  }

  //==========
  // network
  //==========
  login() {
    this.setState({isFetching: true});
    fetch(`${CONFIG.HOST}/users/login`, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        account: this.state.account,
        password: this.state.password,
      }),
    })
      .then(res => res.json())
      .then(data => {
        this.setState({isFetching: false});
        if (!data.ok) {
          switch (data.code) {
            case ERROR.ACCOUNT_NOT_EXIST.code:
              this.setState({tips: '账号不存在，请注册'});
              break;
            case ERROR.WRONG_PASSWORD.code:
              this.setState({tips: '密码错误'});
              break;
            case ERROR.PARSE_FAIL.code:
              this.setState({tips: '输入错误，请检查'});
              break;
            case ERROR.MYSQL_FAIL.code:
              this.setState({tips: '服务器故障，请稍后再试'});
              break;
          }
          return;
        }
        const {token, date, account, userId} = data;
        this.props.navigation.navigate('Main', {
          userId: userId,
          token: {token, date, account},
        });
      })
      .catch(err => {
        this.setState({tips: JSON.stringify(err)});
        console.log(err);
      })
      .finally(() => this.setState({isFetching: false}));
  }

  registerAccount() {
    this.setState({isFetching: true});
    fetch(`${CONFIG.HOST}/users/register`, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        account: this.state.account,
        password: this.state.password,
      }),
    })
      .then(res => res.json())
      .then(data => {
        if (!data.ok) {
          switch (data.code) {
            case ERROR.ACCOUNT_EXIST.code:
              this.setState({tips: '账号已存在，请换一个'});
              break;
            case ERROR.WRONG_PASSWORD.code:
              this.setState({tips: '密码错误'});
              break;
            case ERROR.PARSE_FAIL.code:
              this.setState({tips: '输入错误，请检查'});
              break;
            case ERROR.MYSQL_FAIL.code:
              this.setState({tips: '服务器故障，请稍后再试'});
              break;
          }
          return;
        }

        const {token, date, account, userId} = data;
        this.props.navigation.navigate('Main', {
          userId: userId,
          token: {token, date, account},
        });
      })
      .catch(err => {
        this.setState({tips: '未知错误，请重试'});
        console.log(err);
      })
      .finally(() => this.setState({isFetching: false}));
  }

  render() {
    return (
      <KeyboardAvoidingView style={styles.warpper}>
        <Image source={require('../static/logo.png')} />
        <Text>{this.state.tips}</Text>
        <View style={styles.label}>
          <Text style={styles.text}>账号</Text>
          <View style={styles.input}>
            <TextInput
              style={styles.input}
              value={this.state.account}
              onChangeText={this.onTypeAccount}
              editable={!this.state.isFetching}
              textContentType="username"
            />
          </View>
        </View>

        <View style={styles.label}>
          <Text style={styles.text}>密码</Text>
          <View style={styles.input}>
            <TextInput
              value={this.state.password}
              onChangeText={this.onTypePassword}
              editable={!this.state.isFetching}
              textContentType="password"
              secureTextEntry={true}
            />
          </View>
        </View>

        <View style={styles.btnLine}>
          <View style={styles.cancelBtn}>
            <Button title="登录" onPress={this.onLogin} />
          </View>
          <View style={styles.regBtn}>
            <Button title="注册" color="#AA8" onPress={this.onRegister} />
          </View>
        </View>
      </KeyboardAvoidingView>
    );
  }
}

const styles = StyleSheet.create({
  warpper: {
    height: '100%',
    display: 'flex',
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#eee',
  },

  label: {
    width: '80%',
    padding: 3,
    margin: 10,
    borderRadius: 5,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    backgroundColor: '#FFF',
  },

  btnLine: {
    width: '80%',
    margin: 10,
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'space-between',
  },

  text: {
    borderRadius: 10,
    width: '20%',
    textAlign: 'center',
  },

  input: {
    backgroundColor: '#EEE',
    width: '80%',
    borderRadius: 10,
  },

  regBtn: {
    width: '45%',
  },

  cancelBtn: {
    width: '45%',
  },
});
