import React from 'react';
import {
  StyleSheet,
  View,
  Text,
  TextInput,
  Button,
  TouchableOpacity,
  KeyboardAvoidingView,
} from 'react-native';

import {MapView} from 'react-native-amap3d';
import CONFIG from '../config';

export default class Main extends React.Component {
  static navigationOptions = ({navigation}) => ({
    title: '地心说',
    headerStyle: {
      backgroundColor: '#66ccff',
    },
    headerRight: route => {
      const {navigate, setParams, getParam} = navigation;
      const token = getParam('token');
      if (token) {
        return (
          <Button
            title="退出登录"
            onPress={() => {
              setParams({token: null});
            }}
          />
        );
      }
      return (
        <Button
          title="登录/注册"
          onPress={() => navigate('Register', {userId: getParam('userId')})}
        />
      );
    },
  });

  //==========
  //life cycle
  //==========
  constructor(props) {
    super(props);
    this.message = [];
    this.state = {
      zoomLevel: 10,
      longitude: 116.37296,
      latitude: 39.91095,
      fetchReqTime: null,
      fetchReqPos: {lat: 39.91095, long: 116.37296, zoom: 10},
      chatMsg: '',
      markers: [],
      token: null,
      isSending: false,
    };

    this.onStateChange = this.onStateChange.bind(this);
    this.onSendMessage = this.onSendMessage.bind(this);
    this.onChangeText = this.onChangeText.bind(this);

    this.logicUpdate = this.logicUpdate.bind(this);
    this.messageFilter = this.messageFilter.bind(this);
    this.getToken = this.getToken.bind(this);

    this.sendMessage = this.sendMessage.bind(this);
    this.refreshMessage = this.refreshMessage.bind(this);

    this.timer = setInterval(this.logicUpdate, 100);
  }

  componentDidMount() {
    this.refreshMessage();
  }

  componentWillUnmount() {
    this.timer && clearInterval(this.timer);
  }
  //==========
  // event
  //==========
  onStateChange(status) {
    // 地图状态改变，通常在刷新的时候
    // 改变频率极高
    const {zoomLevel, longitude, latitude} = status.nativeEvent;

    this.setState({
      fetchReqTime: new Date(),
      zoomLevel,
      longitude,
      latitude,
    });
  }

  onChangeText(text) {
    this.setState({
      chatMsg: text,
    });
  }

  onSendMessage() {
    if (this.state.isSending) {
      return;
    }

    this.sendMessage();
  }

  onPress = cluster => {
    this.mapView.setStatus(
      {
        center: cluster.coordinate,
        zoomLevel: this.status.zoomLevel + 1,
      },
      500,
    );
  };

  //==========
  // network
  //==========
  async refreshMessage() {
    const fetchLat = this.state.latitude;
    const fetchLong = this.state.longitude;
    const fetchZoom = this.state.zoomLevel;
    this.setState({
      fetchReqPos: {
        lat: fetchLat,
        long: fetchLong,
        zoomLevel: fetchZoom,
      },
      fetchReqTime: null,
    });
    let token;
    try {
      token = await this.getToken();
    } catch (err) {
      token = {};
    }
    fetch(`${CONFIG.HOST}/message/get`, {
      method: 'POST',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        ...token,
        loc: {
          log: fetchLong,
          lat: fetchLat,
        },
        period: {
          start: -1000 * 60 * 60 * 24,
          end: 1000,
        },
        zoomLevel: this.state.zoomLevel,
      }),
    })
      .then(res => res.json())
      .then(data => {
        if (!data.ok) {
          return;
        }
        let {results} = data;
        let message = this.messageFilter(results);
        this.setState({
          markers: message.map(content => ({
            key: content.msgId,
            coordinate: {latitude: content.loc.y, longitude: content.loc.x},
            msg: content.msg,
            good: content.good,
          })),
        });
      });
  }

  async sendMessage() {
    this.setState({
      isSending: true,
    });

    this.getToken()
      .then(token =>
        fetch(`${CONFIG.HOST}/message/send`, {
          method: 'POST',
          headers: {
            Accept: 'application/json',
            'Content-Type': 'application/json',
          },
          body: JSON.stringify({
            ...token,
            loc: {
              log: this.state.longitude,
              lat: this.state.latitude,
            },
            msg: this.state.chatMsg,
            zoomLevel: this.state.zoomLevel,
          }),
        }),
      )
      .then(res => res.json())
      .then(data => {
        if (!data.ok) {
          this.setState({
            isSending: false,
          });
          return;
        }
        this.refreshMessage();
        this.setState({
          isSending: false,
          chatMsg: ' ',
        });
      })
      .catch(err => {
        console.log(err);
        this.setState({
          isSending: false,
        });
        this.props.navigation.navigate('Register', {
          tips: '你需要注册才能发言',
        });
      });
  }

  //==========
  // helper
  //==========
  getToken() {
    return new Promise((resolve, reject) => {
      const tokenParam = this.props.navigation.getParam('token');
      if (tokenParam) {
        this.setState({tokenParam});
        global.storage.save({key: 'token', data: tokenParam});
        return resolve(tokenParam);
      } else {
        global.storage
          .load({key: 'token'})
          .then(token => {
            this.setState({token});
            return resolve(token);
          })
          .catch(err => {
            reject(err);
          });
      }
    });
  }

  logicUpdate() {
    const now = new Date();
    let newState = {};
    if (this.state.fetchReqTime && now - this.state.fetchReqTime > 200) {
      const {lat, long, zoomLevel} = this.state.fetchReqPos;
      const dis =
        (lat - this.state.latitude) * (lat - this.state.latitude) +
        (long - this.state.longitude) * (long - this.state.longitude);
      // 屏幕移动1/10以上刷新，1/zoomLevel大概是1/100，具体核机型有关
      const threshold = 10 / Math.pow(2, zoomLevel);
      if (
        dis >= threshold ||
        Math.abs(this.state.zoomLevel - zoomLevel) > 0.3
      ) {
        this.refreshMessage();
      }
    }
    this.setState(newState);
  }

  messageFilter(markers) {
    if (!markers) {
      return [];
    }
    const relativeLength = 1 / Math.pow(2, this.state.zoomLevel);
    let output = [];
    // 优先将自己的发言放上去
    // for (let i = 0; i < markers.length; i++) {}
    // 其它发言填充位置
    for (let i = 0; i < markers.length; i++) {
      const {loc, msg} = markers[i];
      let offLoc = {x: loc.x, y: loc.y};
      // 尝试寻找空位放置
      for (let randTry = 0; randTry < 10; randTry++) {
        let offset = {x: 0, y: 0};
        let appear = true;
        for (let j = 0; j < output.length; j++) {
          const diffH = (output[j].loc.y - offLoc.y) / relativeLength;
          const diffW = (output[j].loc.x - offLoc.x) / relativeLength;
          const widthOverlap = (msg.length + output[j].msg.length) * 20;
          if (Math.abs(diffH) <= 20 && Math.abs(diffW) <= widthOverlap) {
            offset.y += diffH;
            offset.x += diffW;
            appear = false;
          }
        }

        if (offset.x === 0 && offset.y === 0) {
          offset.y = -1;
        }

        if (appear) {
          markers[i].loc = offLoc;
          output.push(markers[i]);
          break;
        } else {
          let disSqr = offset.x * offset.x + offset.y * offset.y;
          disSqr = disSqr <= 0 ? 1 : disSqr;
          // 单位向量
          offLoc.x += Math.min(offset.x / disSqr, 1) * relativeLength * 25;
          offLoc.y += Math.min(offset.y / disSqr, 1) * relativeLength * 25;
        }
      }
    }
    return output;
  }

  renderIcon = marker => (
    <View style={styles.bubbleBackground}>
      <Text>{marker.msg}</Text>
    </View>
  );

  renderMarker = item => (
    <MapView.Marker
      key={item.key}
      coordinate={item.coordinate}
      infoWindowDisabled={true}
      marker={item}
      icon={this.renderIcon}
    />
  );

  getClusterRef = ref => (this.cluster = ref);
  getMapRef = ref => (this.mapView = ref);

  render() {
    const props = {
      ref: this.getMapRef,
      style: styles.map,
      rotation: 0,
      rotateEnabled: false,
      onStatusChange: this.onStateChange,
      coordinate: {
        latitude: 39.91095,
        longitude: 116.37296,
      },
    };

    const markers = this.state.markers.map(this.renderMarker);

    return (
      <View style={styles.warpper}>
        <View style={styles.body}>
          <MapView {...props}>{markers}</MapView>
        </View>
        <KeyboardAvoidingView style={styles.chat}>
          <TextInput
            style={styles.chatInput}
            value={this.state.chatMsg}
            onChangeText={this.onChangeText}
            editable={!this.state.isSending}
          />
          <TouchableOpacity
            style={
              this.state.isSending
                ? styles.chatSendBtnDisable
                : styles.chatSendBtn
            }
            onPress={this.onSendMessage}>
            <Text style={styles.chatSendBtnText}>发送</Text>
          </TouchableOpacity>
        </KeyboardAvoidingView>
      </View>
    );
  }
}

const styles = StyleSheet.create({
  warpper: {
    height: '100%',
  },

  header: {
    width: '100%',
    height: 50,
    backgroundColor: '#66ccff',
    justifyContent: 'center',
    alignItems: 'center',
  },
  map: {
    position: 'absolute',
    top: 0,
    bottom: 0,
    left: 0,
    right: 0,
  },

  headerTitle: {
    fontSize: 20,
    color: '#FFF',
  },

  body: {
    flex: 1,
    justifyContent: 'space-between',
    alignItems: 'center',
  },

  chat: {
    width: '100%',
    justifyContent: 'space-around',
    flexDirection: 'row',
    alignItems: 'stretch',
    height: 50,
  },

  chatInput: {
    backgroundColor: '#FFFFFF',
    flexGrow: 5,
    borderRadius: 10,
  },

  chatSendBtn: {
    flexGrow: 1,
    backgroundColor: '#55bbee',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: 10,
  },

  chatSendBtnDisable: {
    flexGrow: 1,
    backgroundColor: '#888888',
    justifyContent: 'center',
    alignItems: 'center',
    borderRadius: 10,
  },

  chatSendBtnText: {
    fontSize: 20,
    color: '#FFF',
  },

  bubbleBackground: {
    backgroundColor: 'rgba(150, 200, 50, 0.5)',
    borderRadius: 5,
  },
});
